header {
    background-color: #081C24;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.header-content {
    width: 1000px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.header-content ul {
    display: -ms-flexbox;
    display: flex;
    width: 30%;
    -ms-flex-align: center;
        align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.link-icon {
    color: #805be7;;
}
.link-text {
    color: #fff;
    font-weight: 600;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
}
.link-text:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
@media screen and (max-width: 1090px) {
    .header-content {
        margin-left: 20px;
        width: 100%
    }
    .header-content ul {
        width: 80%;
    }
}


