.wrap-movies {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.movie-category {
    width: 1000px;
    margin-top: 30px;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 20px;
    color: #555;
}
.movie-content {
    width: 1000px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.movie {
    display: -ms-flexbox;
    display: flex;
    border-radius:3px;
    -webkit-box-shadow:3px 3px 3px 3px #999;
            box-shadow:3px 3px 3px 3px #999;
    margin-bottom: 10px;
    width: 49%;
}
.movie .movie-image {
    width: 30%;
}
.movie .movie-description {
    margin: 0px 10px;
    width: 70%;
}
.movie-description > div {
    text-align: center;
}
.detail-link {
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    color: black;
}
.detail-link:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
.movie img {
    width: 100%;
}
@media screen and (max-width: 1090px) {
    .movie-category {
        width: 100%;
    }
    .movie-content {
        width: 100%;
        padding-left: 10px;
        padding-right: 10px;
    }
    .movie {
        font-size: 85%;
    }
}
@media screen and (max-width: 720px) {
    .movie {
        width: 100%;
        font-size: 100%;
    }
}


